<div id="browse-main"> <EntityTable :tableData="tableData" title="" v-slot="{ entities }" v-if="tableData.length"> <div class="table-container"><div class="column label">Dataset ID</div><div class="column label">Name</div><div class="column label">Description</div><div class="column label">Size</div><template v-for="(row, idx) in entities"><div class="column"> <router-link :to="{ name: &quot;search&quot;, query: {dataset_ids: row[&quot;Dataset ID&quot;], limit: 100, database:$route.query.database}}">{{row["Dataset ID"]}}</router-link><button class="material-symbols-outlined btn btn-secondary p-1 rounded-circle delete-icon" @click="deleteDataset(row[&quot;Dataset ID&quot;])">close</button></div><div class="column">{{row.Name}}</div><div class="column">{{row.Description?.length > 75 ? row.Description.substr(0,75)+"..." : row.Description}}</div><div class="column">{{row.Size}}</div></template></div></EntityTable><div class="loading" :loading="loading" v-else-if="loading"><LoadingSpinner></LoadingSpinner></div><div class="container" :body="body" v-else> <p style="text-align: center">{{body}}</p></div><div class="container"><button class="w-100 btn btn-danger" @click="resetDatabase">Reset Database</button><div class="error-message"><code> </code><pre>{{ traceback }}</pre></div></div></div>